body {
  margin: 0;
}

body:before {
  content: "";
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background: url(/cmb-2024-website/static/media/background-1-landscape.f9d5c761.png) no-repeat center center;
  background-size: cover;
}

@font-face {
  font-family: "Classico";
  src: url(/cmb-2024-website/static/media/classico.cd02569c.ttf) format("truetype");
}

